* {
  box-sizing: border-box;
}

html {
  font-family: "Lato";
  font-size: 10px;
  scroll-behavior: smooth;
}

/* ============ font text ============ */
@font-face {
  font-family: "coolvetica";
  src: url(data:font/opentype;base64,);
  font-style: normal;
  font-weight: 500;
}

/* ============ font icomoon ============ */
@font-face {
  font-family: "icomoon";
  src: url(/fonts/icomoon.eot?ccciao);
  src: url(/fonts/icomoon.eot?ccciao#iefix) format("embedded-opentype"),
    url(/fonts/icomoon.ttf?ccciao) format("truetype"),
    url(/fonts/icomoon.woff?ccciao) format("woff"),
    url(/fonts/icomoon.svg?ccciao#icomoon) format("svg");
  font-weight: 400;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon" !important;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-eye:before {
  content: "\e900";
  font-size: 17px;
}

.icon-gear:before {
  content: "\e901";
}

.icon-mail:before {
  content: "\e902";
  font-size: 18px;
}

.icon-prof:before {
  content: "\e903";
}

.icon-hom:before {
  content: "\e904";
}

/* ============ img responsive ============ */
img {
  max-width: 100%;
  object-fit: cover;
  display: block;
}

p {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  line-height: 1.5;
}

/* ============ fullpage ============ */
#fp-nav {
  ul {
    li {
      a {
        position: relative;
        span {
          background: $color-main;
        }
        &.active {
          & + .fp-tooltip {
            opacity: 1;
            width: auto;
          }
        }
      }
      .fp-tooltip {
        color: $color-main;
        font-family: inherit;
        top: -4px;
      }
    }
  }
  &.home-nav {
    ul {
      li {
        a {
          span {
            background: $color-white;
          }
        }
      }
    }
  }
}



